<template>
	<div>
		<div style="background: #ccc;">
			<div v-for="i in video_arr" style="background: #fff;margin-bottom: 20px;">
				<div style="height: 0.9rem;">
					<div style="float: left;margin-left: 30px;">
						<div style="width: 0.7rem;height: 0.7rem;float: left;border-radius: 50%;margin-top: 10px;">
							<img :src="i.header" style="width: 100%;height: 100%;border-radius: 50%;" />
						</div>
						<span style="font-size: 32px;line-height: 0.9rem;margin-left: 20px;font-weight: bold;">{{i.username}}</span>
						<span style="font-size: 32px;line-height: 0.9rem;margin-left: 20px;font-weight: bold;color: #ccc;">{{i.passtime}}</span>
					</div>
					<span style="font-size: 32px;float: right;line-height: 1rem;color: #CCCCCC;margin-right: 30px;" class="iconfont">&#xe601;</span>
				</div>

				<div>
				<p style="line-height: 0.5rem;margin-left: 30px;margin-right: 30px;">{{i.text}}</p>
			</div>
				<!--/-->
				<div>
					<video width="100%" :src="i.video" controls></video>
				</div>

