					<span style="height: 0.47rem;background: #ccc;font-size: 24px;border-radius: 25px;padding: 10px 15px;line-height: 0.7rem;margin-left: 30px;"></span>
				</div>

				<!---->
				<div style="background: #f8f8f8;width: 90%;margin: 0 auto;border-radius: 10px;">
					<div style="height: 0.9rem;">
						<div style="float: left;margin-left: 30px;">
							<div style="width: 0.7rem;height: 0.7rem;float: left;border-radius: 50%;margin-top: 10px;">
								<img :src="i.top_commentsHeader" style="width: 100%;height: 100%;border-radius: 50%;" />
							</div>
							<span style="font-size: 32px;line-height: 0.9rem;margin-left: 20px;font-weight: bold;">{{i.top_commentsName}}</span>
						</div>

						<span style="font-size: 32px;float: right;line-height: 1rem;color: #CCCCCC;margin-right: 30px;" class="iconfont">&#xe604;</span>
					</div>

					<div>
					<p style="line-height: 0.5rem;margin-left: 30px;margin-right: 30px;">{{i.top_commentsContent}}</p>
					</div>

					<!--/-->
					<div>
						<img :src="i.top_commentsVoiceuri" />
					</div>

				</div>

				<!---->
				<div style="height: 1rem;">
					<ul class="dian">
						<li>
							<i class="iconfont" style="font-size: 44px;">&#xe604;</i>
							<span style="font-size: 22px;"> {{i.down}}</span>
						</li>
						<li>
							<i class="iconfont" style="font-size: 44px;">&#xe633;</i>
							<span style="font-size: 22px;"> {{i.down}}</span>
						</li>
						<li>
							<i class="iconfont" style="font-size: 44px;">&#xe667;</i>
							<span style="font-size: 22px;"> {{i.comment}}</span>
						</li>
						<li>
							<i class="iconfont" style="font-size: 44px;">&#xe65f;</i>
							<span style="font-size: 22px;"> {{i.forward}}</span>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				video_arr: []
			}
		},
		mounted() {
			this.$http.get('https://www.apiopen.top/satinGodApi', {
				params: {
					type: 5,
					page: 1
				}
			}).then((data) => {
				//				console.log(data.data.data)
				this.video_arr = data.data.data
			})
		}

	}
</script>

<style>

</style>


