<template>
	<div>
		<div>
			<ul>
				<li v-for="i in navarr" style="float: left;width: 25%;text-align: center;line-height: 86px;">
					<a :href="i.path" style="font-size: 35px;color: #000000;font-weight: bold;" :style="{color:i==type?'pink':''}"  @click="type=i">{{i.title}}</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				type:0,
				navarr:[
					{
						path:'#/',
						title:''
					},
					{
						path:'#/no2',
						title:''
					},
					{
						path:'#/no3',
						title:''
					},
					{
						path:'#/no4',
						title:''
					},
				]
			}
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
		list-style: none;
		text-decoration: none;
	}
	
</style>


